import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import './Home.css'

const Home = ({ id, go, fetchedUser }) => (
			<div id="screen_1" className="container">
				<div id="header" className="header">

				</div>

				<div className="row">
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-rus');}}>
							<div className="img rus"></div>
							<span className="txt">русский язык</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-math');}}>
							<div className="img math"></div>
							<span className="txt">математика</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-litra');}}>
							<div className="img litra"></div>
							<span className="txt">литература</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-histiry');}}>
							<div className="img history"></div>
							<span className="txt">история</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-soc');}}>
							<div className="img soc"></div>
							<span className="txt">обществознание</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-phys');}}>
							<div className="img phys"></div>
							<span className="txt">физика</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-bio');}}>
							<div className="img bio"></div>
							<span className="txt">биология</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-lang');}}>
							<div className="img lang"></div>
							<span className="txt">английский язык</span>
						</div>
					</div>
					<div className="item_block">
						<div className="item" onClick= {function(){document.getElementById('body').classList.add('grad-chem');}}>
							<div className="img chem"></div>
							<span className="txt">химия</span>
						</div>
					</div>
				</div>
			</div>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
